<template>
  <section>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
      <FormItem label="" prop="title">
        <Input v-model="formValidate.title" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="author">
        <Input v-model="formValidate.author" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="description">
        <Input v-model="formValidate.description" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="keyword">
        <Input v-model="formValidate.keyword" placeholder=""></Input>
      </FormItem>
      <FormItem label="" v-if="categoryList.length > 0">
        <Select v-model="formValidate.category_id">
          <Option v-for="(item, index) in categoryList" :value="item.id" :key="index">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="cover">
        <div class="cover">
          <div class="upload">
            <Upload
              multiple
              type="drag"
              action="http://up-z2.qiniup.com"
              :show-upload-list="false"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :data="{token}">
              <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                <p></p>
              </div>
            </Upload>
          </div>
          <div class="article-cover">
            <img :src="formValidate.cover" alt="cover">
          </div>
        </div>
      </FormItem>
      <FormItem label="" prop="content">
        <mavon-editor
          v-model="formValidate.content"
          :ishljs="true"
          @imgAdd="handleEditorImgAdd"
           @imgDel="handleEditorImgDel"
          ref=md>
        </mavon-editor>

      </FormItem>
      <FormItem>
        <Button @click="handleReset('formValidate')"></Button>
        <Button type="primary" @click="handleSubmit('formValidate')" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
  </section>
</template>
<script>
  import {mapActions} from 'vuex';
  import getUploadToken from '../../libs/upload-token'
  import axios from 'axios'

  export default {
    data() {
      return {
        img_file: [],
        token: '',
        id: this.$route.params.id,
        detail: null,
        categoryList: [],
        formValidate: {
          title: '',
          author: '',
          category_id: '',
          cover: '',
          content: '',
          description: '',
          keyword: ''
        },
        ruleValidate: {
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          author: [
            {required: true, message: '', trigger: 'blur'}
          ],
          cover: [
            {required: true, message: '', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'}
          ],
          keyword: [
            {required: true, message: '', trigger: 'blur'}
          ],
          content: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    created() {
      this._getArticle();
      this._getCategoryList();
      this._getUploadToken();
    },
    methods: {
      ...mapActions({
        getArticle: 'article/getArticle',
        updateArticle: 'article/updateArticle',
        getCategoryList: 'category/getCategoryList'
      }),
      handleEditorImgAdd(pos, $file) {
        //withCredentials cookiecookie
        const axiosInstance = axios.create({withCredentials: false});    
        let data = new FormData();
        data.append('token', this.token); 
        data.append('file', $file);
        axiosInstance({
          method: 'POST',
          url: 'http://up-z2.qiniup.com',  //
          data: data,
          timeout:30000,      //
          onUploadProgress: (progressEvent)=> {
            //imgLoadPercent 
            let imgLoadPercent = Math.round(progressEvent.loaded * 100 / progressEvent.total);
          },
        }).then(res => {
          const key = res.data.key
          const url = `http://qd71k3640.bkt.clouddn.com/${key}`
          this.$refs.md.$img2Url(pos, url)
        })
      },
      handleEditorImgDel(val) {
        delete this.img_file[val];
      },
      // 
      uploadSuccess(response) {
        const url = `http://qd71k3640.bkt.clouddn.com/${response.key}`;
        this.formValidate.cover = url;
        this.$Message.success('!');
      },
      // 
      uploadError(response) {
        this.$Message.success('!');
        console.log(response)
      },
      // token
      async _getUploadToken() {
        try {
          const res = await getUploadToken();
          this.token = res.token;

        } catch (e) {
          console.log(e)
        }
      },
      // 
      async _getArticle() {
        try {
          const res = await this.getArticle({
            id: this.id
          });
          const article = res.data.data;

          this.formValidate.title = article.title;
          this.formValidate.author = article.author;
          this.formValidate.category_id = parseInt(article.category_id);
          this.formValidate.cover = article.cover;
          this.formValidate.content = article.content;
          this.formValidate.description = article.description;
          this.formValidate.keyword = article.description;

        } catch (e) {

        }
      },
      // 
      async _getCategoryList() {
        const res = await this.getCategoryList();
        this.categoryList = res.data.data;
      },
      // 
      async _updateArticle() {
        this.formValidate.id = this.id;

        try {
          await this.updateArticle(this.formValidate);
          this.$Message.success('!');
          this.$router.push('/article');

        } catch (e) {

        }
      },
      // 
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this._updateArticle();

          } else {
            this.$Message.error('!');
          }
        })
      },
      handleReset(name) {
        this.$refs[name].resetFields();
      }
    }
  }
</script>
<style scoped>
  .article-cover {
    width: 120px;
  }

  .article-cover img {
    width: 100%;
  }

  .cover {
    display: flex;
  }

  .cover .upload {
    width: 280px;
    margin-right: 32px;
  }
</style>



