const qiniu = require('qiniu')

// "www.qiniu.com" 
// 
const ACCESS_KEY = "RkN04hhjswEmdBwur5G1SmYlqTR3BZaWc12bs6Hd";
const SECRET_KEY = "SL0XC56q_wp2beHBmdIR0cvJT1YDSUWDU9o1-DNq";

export default async function getUploadToken() {
  return new Promise((resolve, reject) => {
    let mac = new qiniu.auth.digest.Mac(ACCESS_KEY, SECRET_KEY);
    let options = {
      scope: "wusshuang",
      expires: 7200
    };
    let putPolicy = new qiniu.rs.PutPolicy(options);
    let uploadToken = putPolicy.uploadToken(mac);

    if (uploadToken) {
      resolve({
        token: uploadToken
      })
    } else {
      reject()
    }
  })
}




